<template class="header">
    <v-toolbar color="white" class="header-toolbar" height="74" fixed app>
        <v-toolbar-title>
            <img src="../assets/img/logo.png">
        </v-toolbar-title>
        <v-flex xs12 sm6 lg7 offset-sm2 class="mx-auto content-general-search">
            <v-layout row bg-search>
                <v-flex lg8>
                    <v-text-field
                            name="input-1-3"
                            label="Search Workspaces"
                            prepend-icon="search"
                    ></v-text-field>
                </v-flex>
                <v-flex lg4>
                    <v-select
                            label="Workspace"
                            :items="people"
                            v-model="select"
                            item-text="name"
                            item-value="name"
                            max-height="auto"
                            class="select-workspace"
                    >
                        <template slot="selection" slot-scope="data">
                            <span
                                :class="data.item.class"
                                 class="thumb-workspace-header text-md-center mx-auto d-block">
                                  {{ data.item.letter }}
                             </span>
                            {{ data.item.name }}
                        </template>
                      <template slot="item" slot-scope="data">
                            <template v-if="typeof data.item !== 'object'">
                                <v-list-tile-content v-text="data.item"></v-list-tile-content>
                            </template>
                            <template v-else>
                                    <span
                                        :class="data.item.class"
                                        class="thumb-workspace-header text-md-center mx-auto d-block">
                                       {{ data.item.letter }}
                                    </span>
                                <v-list-tile-content>
                                    <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                                </v-list-tile-content>
                            </template>
                        </template>
                    </v-select>
                </v-flex>
            </v-layout>
        </v-flex>
        <v-icon class="text-grey mr-4">fa fa-bookmark</v-icon>


        <v-menu bottom center>
          <v-btn
            fab
            dark
            small
            slot="activator"
            class="icon-profile mr-4">
            AV
          </v-btn>
          <v-list
            class="header-account-list">
            <span class="title-account mb-4 d-block pl-4 ml-1">My Account</span>
              <span class="name-account pl-3 pr-3">
                 <v-btn
                   fab
                   dark
                   smallK K class="icon-profile mr-3 mb-4">KK</v-btn>
                Kletia Kalavace
              </span>

            <v-btn color="transparent" class="user-link" @click.native.stop="dialog = true"><v-icon>settings</v-icon>User Settings</v-btn>
            <v-dialog v-model="dialog" max-width="290">
              <v-card>
                <v-card-title class="headline">User Settings</v-card-title>
                <v-card-text>
                  <v-container grid-list-md>
                    <v-layout wrap>

                  <v-flex xs12 sm6>
                    <v-select
                      label="Language"
                      class="select-language"
                      :items="['English', 'French', 'German', 'Swedish','Albanian']"
                    ></v-select>
                  </v-flex>
                    </v-layout>
                  </v-container>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="green darken-1" flat="flat" @click.native="dialog = false">Disagree</v-btn>
                  <v-btn color="green darken-1" flat="flat" @click.native="dialog = false">Agree</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-list-tile
              v-for="(item, i) in items"
              :key="i"
              @click="">
              <v-list-tile-title>
                <v-icon >{{ item.icon }}</v-icon>
                {{ item.title }}
              </v-list-tile-title>
            </v-list-tile>
          </v-list>

        </v-menu>
    </v-toolbar>
</template>

<script>
    export default {
        name: "header",
        data:() => ({
            people: [
              { name: 'My services', letter: 'M',class:'bg-orange' },
              { name: 'Support', letter: 'S',class:'bg-dark-green'},
              { name: 'ServiceDesk', letter: 'S',class:'bg-light-green'},
              { name: 'ITAM', letter: 'I',class:'bg-light-blue' },
              { name: 'Manager', letter: 'M',class:'bg-dark-grey' },
            ],
            items: [
              { title: 'Logout',icon:'power_settings_new' },
            ],
      dialog: false,
        }),
    }
</script>

<style lang="scss">
    @import '~@/assets/sass/variables.scss';
    .thumb-workspace-header{
        height:23px;
        width:23px;
        font-size: 12px;
        color:#fff;
        border-radius: 50%;
        line-height: 23px;
        margin-right: 14px !important;
        margin-left: 3px !important;
    }
    .menu__content.menu__content--select{
      top: 13px !important;}
    .select-workspace{
      .menu__content.menu__content--select{
        top: 13px !important;
        .list{
          padding-top:0 !important;
          & > div{
            &:nth-child(1){
              height: 49px;
              border-bottom: 1px solid #eee;
              margin-bottom: 10px!important;
              .list__tile.list__tile--link{
                height: 48px !important;
              }
              a{
                height: 100%;
              }

            }
          }
          & >div{
            height: 35px;
          }

        }
      }
    }


    .list__tile.list__tile--link{
        position: relative;
        height: 35px;
        &:hover{
            background: #eeeeee !important;
        }
        .list__tile__content{
            height: 24px !important;
        }
    }

    .bg-orange{
        background-color: $bg-orange;
    }
    .bg-dark-green{
        background-color: $bg-dark-green;
    }
    .bg-light-green{
        background-color: $bg-light-green;
    }
    .bg-light-blue{
        background-color: $bg-light-blue;
    }
    .bg-dark-grey{
        background-color: $bg-dark-grey;
    }
    .header-toolbar{
        z-index: 99;
        .text-grey{
            color:#9ba9bb;
        }
        .icon-profile{
            height:46px;
            width:46px;
        }
        .bg-search{
            background-color:$bg-search;
        }
    }
    .content-general-search{
        max-width: 729px !important;
        .input-group.input-group--text-field{
            padding: 9px 15px;
            label{
                top: 10px;
                font-size: 15px;
                color:$text-search;
                left: 52px;
            }
            .input-group__input i{
                color:$text-search;
            }
        }
        .input-group.input-group--text-field.input-group--select{
            label{
                color:$text-select;
                font-size: 15px;
                font-weight: 500;
            }
            .input-group__selections{
                color:$text-select;
                font-size: 15px;
                font-weight: 500;
                .avatar{
                    width:22px !important;
                    height:22px !important;
                    margin-right:10px;
                }
            }
        }
        .input-group--dirty{
            label{
                opacity: 0 !important;
                transform: none !important;
            }
        }
        .input-group__details{
            display: none;
        }
        .input-group--focused{
            label{
                opacity: 0 !important;
                transform: none !important;
            }
        }

    }
    .menu__content--select{
        .list__tile__content{
            .list__tile__title{
                color:$text-select;
                font-size: 15px;
                font-weight: 500;
            }
        }
        .list__tile__avatar{
            min-width: 34px;
            .avatar{
                width:22px !important;
                height:22px !important;
            }
        }
    }
  .header-account-list{
    padding:20px 0;
    color:$blue--text;
    .title-account{
      font-size: 20px;
      font-weight: 500;
      color:$blue--text;
    }
    .name-account{
      font-size: 18px;
      display: block;
      .btn--floating{
        width: 48px;
        height:48px;
        background-color: $bg-navigation-open;
      }
    }
    .user-link{
      font-size: 15px;
      font-weight: 500;
      padding-left: 15px;
      color:$blue--text;
      box-shadow: none;
      text-transform: none;
      width: 100%;
      margin-left: 0;
      .icon{
        color:$text-search;
        margin-right: 37px;
        margin-left: -32px;
      }
    }
    .list__tile--link{
      margin-bottom: 12px;
    }
    .list__tile__title{
      font-size: 15px;
      font-weight: 500;
      padding-left:15px;
      .icon{
        color:$text-search;
        margin-right: 34px;
      }
    }
  }


</style>



